<template>
  <div class="container">
    <div class="one">
      <div>
        <el-row :gutter="20" class="el-row1">
          <el-col :span="6">
            财务明细
          </el-col>
        </el-row>
      </div>
      <div>
        <el-row :gutter="20" class="el-row1">
          <el-col :span="3" class="el-col1">
            <span>备用金</span>
            <span>1</span>
          </el-col>
          <el-col :span="3" class="el-col1">
            <span>支出</span>
            <span>1</span>
          </el-col>
          <el-col :span="3" class="el-col1">
            <span>剩余</span>
            <span>1</span>
          </el-col>
        </el-row>
      </div>
      <div>
        <el-row :gutter="20" class="el-row1">
          <el-col :span="3" class="el-col1">
            备用币
          </el-col>
          <el-col :span="3" class="el-col1">
            总币
          </el-col>
          <el-col :span="3" class="el-col1">
            支出
          </el-col>
          <el-col :span="3" class="el-col1">
            结余
          </el-col>
        </el-row>
      </div>
      <div>
        <el-row :gutter="20" class="el-row2">
          <el-col :span="3" class="el-col1">
            name
          </el-col>
          <el-col :span="3" class="el-col1">
            100
          </el-col>
          <el-col :span="3" class="el-col1">
            50
          </el-col>
          <el-col :span="3" class="el-col1">
            50
          </el-col>
        </el-row>
      </div>
    </div>
    <div class="one">
      <div v-if="true">{{userData.trader_name}}</div>
      <el-row :gutter="20" class="el-row1">
        <el-col :span="12">
          <span style="margin-right: 10px">预估总资产:</span>
          <span>{{userData.total}}￥</span>
          <span>≈</span>
          <span>{{userData.total_btc}}β</span>
        </el-col>
      </el-row>
      <el-row :gutter="20" class="el-row1">
        <el-col :span="24" class="assertitem">
          <div style="margin-right: 10px">
            <span style="margin-right: 15px">现金资产:</span>
            <span>{{userData.entity}}￥</span>
          </div>
          <div v-if="tranderFlag">
            <span>(&nbsp;</span>
            <span>支付宝：{{userData.ALIPAY}}</span>
            <span style="margin: 0 20px">微信：{{userData.WEIXIN}}</span>
            <span>银行卡：{{userData.BANK}}</span>
            <span>&nbsp;)</span>
          </div>
        </el-col>
      </el-row>
      <el-row :gutter="20" class="el-row1">
        <el-col :span="24"  class="assertitem">
          <div style="margin-right: 10px">
            <span style="margin-right: 25px">币资产:</span>
            <span>{{userData.fictitious}}&nbsp;β</span>
          </div>
          <div v-if="tranderFlag" style="display: flex">
            <span>(&nbsp;</span>
            <div style="margin-right: 20px" v-for="item in userData.detail">
              <span>{{item.symbol}}:</span>
              <span>{{item.amount}}</span>
            </div>
            <!--<span>etc：33</span>-->
            <!--<span>ltc：33</span>-->
            <span>&nbsp;)</span>
          </div>
        </el-col>
      </el-row>
    </div>
    <div>
      <div class="itemCommon" style="margin-bottom: 10px">
        <span class="itemLeft">查询交易者信息</span>
        <div class="nav_list">
          <el-select @change="getTrander" v-model="trander" filterable clearable placeholder="请选择">
            <el-option-group
              v-for="group in tranderList"
              :key="group.label"
              :label="group.label">
              <el-option
                v-for="item in group.options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-option-group>
          </el-select>
        </div>
      </div>
    </div>
    <div class="tranderContent">
      <div class="one person" v-for="item in tranderInfo">
        <p>{{item.trader_name}}</p>
        <div style="width: 100%">
          <el-row :gutter="20" class="el-row1">
            <el-col :span="6">
              <span>持有总资产</span>
              <span>≈</span>
              <span>{{item.fictitious}}￥</span>
            </el-col>
            <el-col :span="6">
              <span>现金资产</span>
              <span>{{item.entity}}￥</span>
            </el-col>
            <el-col :span="6">
              <span>币资产</span>
              <span>≈</span>
              <span>{{item.total_btc}}</span>
            </el-col>
          </el-row>
          <el-row :gutter="20" class="el-row2">
            <el-col :span="6" :key="index" v-for="(item1,index) in item.detail">
              <span>{{item1.symbol}}</span>
              <span>{{item1.balance}}</span>
            </el-col>
          </el-row>
        </div>
      </div>
      <div class="pages">
        <el-pagination
          background
          layout="prev, pager, next"
          @current-change="pageChange"
          :page-size="5"
          :total="100">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
  import { httpGet } from '../../utils/util'
  import { mapState } from 'vuex'
  export default {
    data() {
      return {
        userData: {},
        page: 1,
        per_page: 5,
        tranderList: [],
        trander: '',
        tranderInfo: [],
        tranderFlag: false
      }
    },
    computed: {
      ...mapState(["userInfo"])
    },
    mounted() {
      this.getData();
      this.getAllWallet(this.page,this.per_page);
      this.TraderObject();
    },
    methods: {
      //头部资产信息 总资产部分
      getData() {
        var that = this;
        httpGet("/wallet_price/total",{
          sid: that.userInfo.sid
        },function (res) {
          if(res.data.error_code == 0){
            that.userData = res.data.total;
            //console.log(res);
          }
        })
      },
      //获取所有交易员钱包信息
      getAllWallet(p,per) {
        var that = this;
        httpGet("/wallet_price/all_profile",{
          page: p,
          per_page: per,
          sid: that.userInfo.sid
        },function (res) {
          console.log(res);
          if(res.data.error_code == 0) {
            that.tranderInfo = res.data.all_wallet_price;
            that.toallPages = Math.ceil(res.data.all_wallet_price.length/that.per_page);
            console.log(res.data.all_wallet_price,res.data.all_wallet_price.length,that.per_page,that.toallPages)
          }
        })
      },
      //获取交易者列表
      TraderObject() {
        var that = this;
        httpGet("/traders/profile",{
          type: 1,
          sid: that.userInfo.sid
        },function (res) {
//          console.log(res);
          if (res.data.error_code == 0) {
            var traders = res.data.trader;
            var obj = {};
            var arr = [];
            for(var key in traders){
              var item = {};
              item.value = traders[key].id;
              item.label = traders[key].name;
              arr.push(item);
            }
            obj.label = "内部人员";
            obj.options = arr;
            that.tranderList.push(obj);
          }
        });
        httpGet("/traders/profile",{
          type: 2,
          sid: that.userInfo.sid
        },function (res) {
//          console.log(res);
          if (res.data.error_code == 0) {
            var traders = res.data.trader;
            var obj = {};
            var arr = [];
            for(var key in traders){
              var item = {};
              item.value = traders[key].id;
              item.label = traders[key].name;
              arr.push(item);
            }
            obj.label = "外部人员";
            obj.options = arr;
            that.tranderList.push(obj);
          }
//          console.log("11",that.tranderList)
        });
      },
      //点击查询交易者信息时获取指定交易员的信息
      getTrander() {
        console.log(this.trander);
        var that = this;
        httpGet("/wallet_price/profile",{
          start_time: '',
          end_time: '',
          trader_id: that.trander,
          sid: that.userInfo.sid
        },function (res) {
          console.log(res);
          if(res.data.error_code == 0) {
            if(!that.trander){
              that.tranderFlag = false;
              that.getData();
            }else {
              that.userData = res.data.all_wallet_price[0];
              that.tranderFlag = true;
            }
            console.log("userData",that.userData);
          }
        })
      },
      //响应分页函数
      pageChange(value) {
        console.log(value);
        this.getAllWallet(value,this.per_page);
      }
    }
  }
</script>
<style scoped>
  .one{
    border-bottom: 1px solid #ddd;
    padding-bottom: 10px;
  }
  .el-row1{
    margin-top: 20px;
  }
  .el-row2{
    margin-top: 5px;
  }
  .el-col1{
    /*border: 1px solid red;*/
    display: flex;
    justify-content: space-between;
    margin-right: 40px;
  }
  .el-col{
    /*text-align: center;*/
  }
  .person{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  .person p{
    /*border: 1px solid red;*/
    width: 90px;
    font-size:20px;
    color: #000;
    font-weight: bold;
  }
  .person >div{
    /*border: 1px solid red;*/
  }
  .itemCommon{
    height: 60px;
    line-height: 60px;
    display: flex;
  }
  .itemCommon .itemLeft{
    margin-right: 20px;
  }
  .tranderContent{
    width: 100%;
    height: auto;
    overflow: auto;
  }
  .assertitem{
    display: flex;
  }
  .pages{
    /*height: 50px;*/
    display: flex;
    flex-direction: row-reverse;
  }
</style>




